import React, {Component} from 'react';
import './App.css';
import rooms from './roomlist';


class Rooms extends Component{
  constructor (props){
    super(props);
    this.state = {
      moveMsg: "",
      currentroom: rooms[0],
            }
this.n = this.n.bind(this);
this.s = this.s.bind(this);
this.e = this.e.bind(this);
this.w = this.w.bind(this);
// this.test = this.test.bind(this);
  }


componentDidMount(){

}

// test(event){
//   console.log(event.target.value)
// }


n(){
let target = rooms.filter(room => room.id == this.state.currentroom.north)
target = target[0]
if (this.state.currentroom.north) {
  this.setState({moveMsg: "You walk to the north"})
  this.setState({name: "", desc: ""});
  setTimeout(() => {this.setState({moveMsg: ""})}, 1000);
  setTimeout(() => {this.setState({currentroom: target})}, 1000);
}
else {
  this.setState({moveMsg: "There's no exit in that direction!"})
}
}

s(){
  let target = rooms.filter(room => room.id == this.state.currentroom.south)
  target = target[0]
  if (this.state.currentroom.south) {
    this.setState({moveMsg: "You walk to the south."})
    this.setState({name: "", desc: ""});
    setTimeout(() => {this.setState({moveMsg: ""})}, 1000);
    setTimeout(() => {this.setState({currentroom: target})}, 1000);
  }
  else {
    this.setState({moveMsg: "There's no exit in that direction!"})
  }
  }

e(){
  let target = rooms.filter(room => room.id == this.state.currentroom.east)
  target = target[0]
  if (this.state.currentroom.east) {
    this.setState({moveMsg: "You walk to the east."})
    this.setState({name: "", desc: ""});
    setTimeout(() => {this.setState({moveMsg: ""})}, 1000);
    setTimeout(() => {this.setState({currentroom: target})}, 1000);
  }
  else {
    this.setState({moveMsg: "There's no exit in that direction!"})
  }
  }

w(){
  let target = rooms.filter(room => room.id == this.state.currentroom.west)
  target = target[0]
  if (this.state.currentroom.west) {
    this.setState({moveMsg: "You walk to the west."})
    this.setState({name: "", desc: ""});
    setTimeout(() => {this.setState({moveMsg: ""})}, 1000);
    setTimeout(() => {this.setState({currentroom: target})}, 1000);
  }
  else {
    this.setState({moveMsg: "There's no exit in that direction!"})
  }
  }

  render(){
    const roomname = this.state.currentroom.name;
    const desc = this.state.currentroom.desc;
    const nsew = <>
    {this.state.currentroom.north ? <span onClick={this.n}>{`<N>`}</span> : null}
    {this.state.currentroom.south ? <span onClick={this.s}>{`<S>`}</span> : null}
    {this.state.currentroom.east ? <span onClick={this.e}>{`<E>`}</span> : null}
    {this.state.currentroom.west ?<span onClick={this.w}>{`<W>`}</span> : null}
    </>
    let moveMsg = this.state.moveMsg




  return (
    <div>
    <h1>{roomname}</h1>
    <p id="moveMsg">{moveMsg}</p>
    <p>{desc}</p>
    <p id="nsew">{nsew}</p>
    </div>
  );
}
}
export default Rooms;
